<!-- 【用户信息】弹窗页面 -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item label="用户头像" prop="loginName">
        <sui-photo @refreshPhoto="refreshPhoto" v-model="sysUser.photo" :disabled="dialog.disabled" :url="sysUser.photo">11</sui-photo>
      </el-form-item>
      <el-form-item label="登录名" prop="loginName">
        <el-input v-model="sysUser.loginName" placeholder="请输入登录名" :disabled="true" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="姓名" prop="userName">
        <el-input v-model="sysUser.userName" placeholder="请输入姓名" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="手机号" prop="phone">
        <el-input v-model="sysUser.phone" placeholder="请输入手机号" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="sysUser.email" placeholder="请输入邮箱" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="所属角色" prop="roleList">
        <el-checkbox-group v-model="checkRoleList">
          <el-checkbox v-for="(item,index) in roleList" :disabled="true"  border size="medium" :key="index" :label="item" >{{item.roleName}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysUserForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "../../../components/sui-photo/sui-photo";
  export default {
    components: {suiPhoto},
    name: "sysUserForm",
    data() {
      return {
        roleList: [],
        checkRoleList: ["普通用户"],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        sysUser: {
          photo: "",
          loginName: "",
          password: "",
          userName: "",
          userNo: "",
          email: "",
          phone: "",
          officeId: ""
        },
        //表单校验
        rules: {
        }
      }
    },
    mounted() {
    },
    methods: {
      refreshPhoto(url){
        this.sysUser.photo = url;
      },
      getRoleList(userId){
        const that = this;
        that.$request.doGet('/api/sysUser/findRolesByUserId?userId='+userId).then(res => {
          if (res.isOk) {
            that.roleList = res.list;
            this.checkRoleList = that.roleList.filter(function (v) {
              if (v.checked) {return v}
            });
          }
        });
      },
      //打开新增页面
      openDialogAdd(title) {
        this.getRoleList()
        this.showDialog(title, false);
      },
      //打开查看页面
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysUser = res.obj;
            this.getRoleList(this.sysUser.id)
          }
        });
      },
      //打开编辑页面
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysUser = res.obj;
            this.getRoleList(this.sysUser.id)
          }
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            if(that.checkRoleList.length==0){
              that.showWarn("请选择角色");
              return false
            }
            that.sysUser.roleList=that.checkRoleList.map(function (v) {
              return v.roleId
            });
            that.$request.doPost('/api/login/updateUserInfo',that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.sysUser);
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      },
    }
  }
</script>

<style scoped>
</style>



