<template>
  <el-container>
    <!-- 表单弹窗 -->
    <userInfo-form ref="userInfoDialog"/>
    <password-form ref="PasswordDialog" @reloadList="getList"/>
    <el-aside :class="[{'aside-hidden': isHidden}]">
      <el-menu
        class="el-menu-demo"
        background-color="#333"
        text-color="#fff"
        active-text-color="#409EFF"
        :default-active="$route.path" @open="handleOpen" @close="handleClose" :collapse="isCollapse" router>
        <el-submenu v-if="menu.children&&!menu.href" v-for="(menu,index) in menuList" :key="index"  :index="index">
          <template slot="title">
            <i :class="menu.icon"></i><span class="span" slot="title">{{menu.name}}</span>
          </template>
          <el-menu-item @click="addTab(item)" :index="item.href" v-for="(item,childrenIndex) in menu.children" :key="childrenIndex">
            <span class="span" slot="title">{{item.name}}</span>
          </el-menu-item>
        </el-submenu>
        <el-menu-item class="el-submenu__title" v-else @click="addTab(menu)" :index="menu.href">
          <i :class="menu.icon"></i><span slot="title" class="span">{{menu.name}}</span>
        </el-menu-item>
      </el-menu>
      <el-button size="small" type="primary" :class="['btn-aside',{'aside-hidden': isHidden}]" class="el-icon-s-grid" @click="collapse"></el-button>
    </el-aside>

    <el-container style="height: 100vh">
      <el-header style="text-align: right; font-size: 12px;background: #3c8dbc">
        <div class="project-name">{{$global.PROJECT_NAME}}</div>
        <el-dropdown>
          <i class="el-icon-setting userInfo"  style=""><span v-for="roleName in userInfo.roleList">【{{roleName}}】</span></i>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="handleUserView">用户信息</el-dropdown-item>
            <el-dropdown-item @click.native="handlePasswordEdit">修改密码</el-dropdown-item>
            <el-dropdown-item @click.native="loginOut">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-image class="head-img" :src="formatUrl(userInfo.photo)"></el-image>
        <span style="color: white;font-weight: bold;font-size: medium">{{userInfo.loginName}}</span>
      </el-header>
      <el-tabs v-model="editableTabsValue" type="border-card" style="background: white;border: none" closable @tab-click="changeTab" @tab-remove="removeTab">
        <el-tab-pane v-for="(item, index) in editableTabs" :key="item.name" :label="item.title" :name="item.href">
        </el-tab-pane>
      </el-tabs>
      <el-main style="background: white">
          <router-view></router-view>
      </el-main>
      <el-footer class="copyright">Copyright © 2021 by {{$global.PROJECT_NAME}}</el-footer>
    </el-container>
  </el-container>
</template>
<script>
  import userInfoForm from '../user/userInfo'
  import passwordForm from '../user/userPassword'
  import {mapActions} from "vuex";
  export default {
    components: {userInfoForm,passwordForm},
    data() {
      return {
        isHidden:false,
        isCollapse: false,
        editableTabsValue: '/home',
        editableTabs: [{
          title: '首页',
          href: '/home',
        }],
        menuList:[],
      }
    },
    mounted() {
      if (this.$route.path !== '/home') {
        this.changeRouter("/home");
      }
      this.$request.getMenuUserTree().then(res=>{
        if(res.isOk){
          this.menuList=res.list
        }else{
          const that = this;
          that.showConfirm("系统过期,请重新登录!",function () {
            that.$router.push('/login');
          })
        }
      })
    },
    computed:{
      userInfo(){
        return this.$store.getters.userInfo
      },
      basePath(){
        return this.$global.FILE_BASE_PATH
      }
    },
    methods: {
      ...mapActions(['commitLoginOut']),
      //点击查看
      handleUserView() {
        this.$refs.userInfoDialog.openDialogEdit('用户信息', '/api/sysUser/get?id=' + this.$store.getters.userInfo.id);
      },
      handlePasswordEdit() {
        this.$refs.PasswordDialog.openDialogEdit('修改密码', '/api/sysUser/get?id=' + this.$store.getters.userInfo.id);
      },
      loginOut(){
        const that = this;
        that.showConfirm('确认退出吗?',function (){
          that.commitLoginOut().then(res=>{
            if(res.isOk){
              that.showToast("退出成功");
              that.$router.push('/login');
            }
          })
        })
      },
      collapse() {
        this.isCollapse=!this.isCollapse
        if(this.isCollapse){
          this.isHidden=true
        }else{
          this.isHidden=false
        }
      },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      changeRouter(targetHref){
        this.$router.push(targetHref); //跳转到指定页面
      },
      changeTab(menu){
        this.$router.push(menu.name); //跳转到指定页面
      },
      addTab(menu) {
        let tabs = this.editableTabs.map(function (v) {
          return v.href
        })
        let hasTab = tabs.contain(menu.href);
        if(!hasTab){
          this.editableTabs.push({
            title: menu.name,
            href: menu.href,
          });
        }
        this.editableTabsValue = menu.href;
      },
      removeTab(targetHref) {
        let tabs = this.editableTabs;
        let activeName = this.editableTabsValue;
        if (activeName === targetHref) {
          tabs.forEach((tab, index) => {
            if (tab.href === targetHref) {
              let nextTab = tabs[index + 1] || tabs[index - 1];
              if (nextTab) {
                activeName = nextTab.href;
                this.changeRouter(nextTab.href)
              }
            }
          });
        }
        this.editableTabsValue = activeName;
        this.editableTabs = tabs.filter(tab => tab.href !== targetHref);
      }
    }
  };
</script>
<style scoped>
  .userInfo{
    margin-right: 15px;
    color: white;
    font-weight: bold;
    font-size: medium;
    padding: 10px
  }
  .userInfo:hover{
    border-radius: 5px;
    background: rgba(48, 65, 86, 0.21);
  }
  .head-img {
    position: relative;top:10px;
    width: 35px;
    height: 35px;
    border-radius: 50%
  }
  .span{
    margin-left: 5px;
  }
  .copyright {
    border-top: 1px solid #e7eaec;
    background: white;
    padding: 5px;
    height: 30px!important;
    text-align: center;
    font-size: smaller;
    color: grey;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 250px;
    min-height: 250px;
  }
  .el-header {
    background-color: white;
    color: #304156;
    line-height: 60px;
    height: 100%;
  }
  aside{
    padding: 0;
    background: rgb(238, 241, 246);
  }
  .el-aside {
    height: 900px;
    transition: width 0.3s;
    -webkit-transition:width 0.3s;
    background: #304156;
    width: 250px!important;
  }
  .el-aside .el-menu{
    border-right: none;
  }
  .btn-aside {
    background: #304156;
    border: none;
    height: 30px;
    /*border-radius: 5px;*/
    position: fixed;
    bottom: 0;
    transition: width 0.3s;
    -webkit-transition:width 0.3s;
    width: 250px!important;
  }
  .footer-aside {
    z-index: 1000;
    background: #304156;
    border: none;
    height: 30px;
    /*border-radius: 5px;*/
    position: fixed;
    bottom: 0;
    transition: width 0.3s;
    -webkit-transition:width 0.3s;
  }
  .aside-hidden {
    transition: width 0.3s;
    -webkit-transition:width 0.3s;
    width: 64px!important;
  }

  .project-name{
    float: left;
    font-size: 30px;
    text-shadow: 1px 3px 3px #000000;
    color: white;
    font-weight: bold;
    text-align: center;
  }


  ::-webkit-scrollbar {
    width:12px;
  }
</style>



